
import { defineComponent } from "vue";
export default defineComponent({
  ControlType: "ColorSelect", // 必须与文件名匹配
  nameCn: "颜色选择",
  icon: "icon-sen103",
  formConfig: {
    data() {
      return {
        fieldName: "",
        label: "标签名称",
        tip: "",
        placeholder: "",
        showRule: "{}",
        required: false,
        rule: "[]",
        default: '#409EFF',
      };
    },
    morenConfig() {
      return [
        {
          ControlType: "ColorSelect",
          data: {
            fieldName: "default",
            tip: "",
            label: "默认值",
            placeholder: "",
            showRule: "{}",
            required: false,
            rule: "[]",
          },
        },
      ];
    },
  },
  props: {
    drag: Boolean,
    data: Object,
    item: Object,
  },
  watch: {
    data: {
      handler() {
        this.$emit("change");
      },
      deep: true,
    },
  },
});
