
import { defineComponent, reactive, toRefs } from "vue";

export default defineComponent({
    setup(){
        let keyList = reactive([
            {
              key: 'ctrl + C',
              value: '复制组件'  
            },
            {
              key: 'ctrl + V',
              value: '粘贴组件'  
            },
            {
              key: 'ctrl + X',
              value: '裁剪组件'  
            },
            {
              key: 'delete',
              value: '删除组件'  
            },
            {
              key: 'ctrl + E',
              value: '清空画布'  
            },
            {
              key: 'ctrl + ↑',
              value: '组件上移'  
            },
            {
              key: 'ctrl + ↓',
              value: '组件下移'  
            },
            {
              key: '↑',
              value: '选中上移'  
            },
            {
              key: '↓',
              value: '选中下移'  
            },
        ])
        return {
            keyList
        }
    }
});
