
import { defineComponent } from "vue";
export default defineComponent({
  ControlType: "Switch", // 必须与文件名匹配
  nameCn: "开关",
  icon: "icon-kaiguanguan",
  formConfig: {
    data() {
      return {
        fieldName: "",
        label: "标签名称",
        tip: "",
        value: '',
        showRule: "{}",
        required: false,
        rule: "[]",
        default:  false,
      };
    },
    morenConfig() {
      return [
        {
          ControlType: "Switch",
          data: {
            fieldName: "default",
            tip: "",
            label: "默认值",
            showRule: "{}",
            required: false,
            default: false,
            rule: "[]",
          },
        }
      ];
    },
  },
  props: {
    drag: Boolean,
    data: Object,
    item: Object,
  },
  watch: {
    data: {
      handler(){
        this.$emit('change')
      },
      deep: true
    }
  },
});
